<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style scoped>

    .w-100 {
        min-width: 0;
    }

    .input-group-addon {
        padding: 0.375rem;
        font-size: 0.9rem;
    }

    td .multiselect {
        width: 5rem;
    }

    td .multiselect__single {
        font-size: 0.9em;
    }

    table {
        min-width: 1280px;
    }

    .basicinfo >>> .vdp-datepicker input {
        padding: 0.375rem 0.75rem !important;
        border: 1px solid #e8e8e8;
        border-radius: 0.25rem;
        font-size: 1rem;
        line-height: 1.5;
    }

    .basicinfo >>> .multiselect__placeholder {
        margin-bottom: 0;
    }

    .basicinfo >>> .multiselect__tag {
        margin-bottom: 0;
    }

    .basicinfo >>> .multiselect__tags {
        font-size: 16px;
        margin-bottom: 0;
        line-height: 25px;
        padding: 5px 40px 0 10px !important;
    }

    .exchangefee >>> .multiselect__tags {
        border-radius: 5px 0 0 5px;
    }

    #assets {
        font-size: 14px;
    }

    .basicinfo >>> .multiselect--disabled {
        opacity: 1;
    }

    .basicinfo >>> .form-control {
        border: 1px solid #e8e8e8;
    }

    .basicinfo >>> .multiselect__single,
    .basicinfo >>> .multiselect__input {
        line-height: 28px;
        padding-left: 0;
        margin-bottom: 0px;
    }

    table.inputtrans td, table.inputtrans th {
        vertical-align: middle;
    }

    .copytoclip {
      width: 36px;
    }

    .copytoclip > span:nth-child(2),
    .copytoclip:hover > span:nth-child(1) {
      display: none;
    }
    .copytoclip:hover > span:nth-child(2) {
      display: block;
    }

    @media only screen and (max-width: 1280px) {
        .list-group-item {
            border: none;
        }
    }
</style>



