<template>
  <div>
    <div class="alert alert-danger text-center" v-if="error">
      {{ error }}
    </div>

    <div class="row justify-content-center">
        <div class="form-inline">
          <label for="year" class="mr-4">Select a tax year:</label>
          <b-form-select id="year" :options="years.options" @input="onSelect"></b-form-select>
        </div>
    </div>
    <div v-if="transactions && transactions.length !== 0">
        <div class="row justify-content-center">
            <h3 class="mt-5">Tax result by transactions</h3>
        </div>
        <div class="row my-3 justify-content-center">
          <table class="table table-striped" v-if="ready">
            <thead>
              <tr>
                <th scope="col" style="width:130px">Date</th>
                <th scope="col">Transaction number</th>
                <th scope="col">Transaction type</th>
                <th scope="col">Sold currency</th>
                <th scope="col">Sold currency<br>amount</th>
                <th scope="col">Sale proceeds (BGN)</th>
                <th scope="col">Cost basis (BGN)</th>
                <th scope="col">Profit/Loss (BGN)</th>
              </tr>
            </thead>
              <tbody>
              <tr v-for="tx in transactions">
                <td>{{ tx.date }}</td>
                <td>{{ tx.transactionId }}</td>
                <td>{{ tx.type }}</td>

                <td>{{ tx.soldCcy }}</td>
                <td class="text-right">{{ parseFloat(tx.soldCcyAmount).toFixed(8) }}</td>

                <td class="text-right">{{ parseFloat(tx.salesProceedsBGN).toFixed(2) }}</td>
                <td class="text-right"><span v-if="tx.costBasisBGN">{{ parseFloat(tx.costBasisBGN).toFixed(2) }}</span></td>
                <td class="text-right"><span v-if="tx.profitLoss">{{ parseFloat(tx.profitLoss).toFixed(2) }}</span><span v-else>0.00</span></td>
              </tr>
            </tbody>
              <tfoot>
              <tr>
                  <td class="text-right" colspan="7"><b>Net result:</b></td>
                  <td class="text-right"><b>{{ parseFloat(endresult).toFixed(2)}}</b></td>
              </tr>

              </tfoot>
          </table>
        </div>
        <div class="row justify-content-center" id="pafdm">
            <h3 class="mt-5">Tax result by date</h3>
        </div>
        <div v-if="days" class="row my-3 justify-content-center">
            <table class="table table-striped">
                <thead>
                <tr>
                    <th scope="col">Date</th>
                    <th scope="col">Sale proceeds</th>
                    <th scope="col">Cost basis</th>
                    <th scope="col">Total profit</th>
                    <th scope="col">Total loss</th>
                </tr>
                </thead>
                <tbody>
                    <tr v-for="(value, name) in days">
                        <td>{{ name }}</td>
                        <td class="text-right"><span v-if="value.proceeds">{{ parseFloat(value.proceeds).toFixed(2) }}</span><span v-else>0.00</span></td>
                        <td class="text-right"><span v-if="value.price">{{ parseFloat(value.price).toFixed(2) }}</span><span v-else>0.00</span></td>
                        <td class="text-right"><span v-if="value.profitLoss >= 0">{{ parseFloat(value.profitLoss).toFixed(2) }}</span><span v-else>0.00</span></td>
                        <td class="text-right"><span v-if="value.profitLoss <= 0">{{ parseFloat(value.profitLoss).toFixed(2) }}</span><span v-else>0.00</span></td>
                    </tr>
                </tbody>
            </table>
        </div>
        <div v-if="endsums" class="row my-5 justify-content-center">
            <table class="table table-striped text-right" v-if="ready">
                <thead>
                <tr>
                    <th class="text-left" scope="col">Currency</th>
                    <th scope="col">Quantity<br>on {{ enddate }}</th>
                    <th scope="col">Average cost basis<br>on {{ enddate }}</th>
                </tr>
                </thead>
                <tbody>
                <template v-for="(value, name) in endsums">
                    <tr>
                    <td class="text-left">{{ name }}</td>
                    <td><span v-if="value.quantity">{{ parseFloat(value.quantity).toFixed(8)}}</span></td>
                    <td><span v-if="value.price && parseFloat(value.quantity).toFixed(8) !== '0.00000000'">{{ parseFloat(value.price).toFixed(2) }}</span><span v-else>0.00</span></td>
                    </tr>
                </template>
                </tbody>
            </table>
        </div>
        <div class="row my-5 justify-content-center">
            <button @click="pdf()" class="btn btn-primary"><span class="glyphicon glyphicon-download-alt"></span> Download as PDF</button>&nbsp;&nbsp;
            <button @click="csvExport()" class="btn btn-primary"><span class="glyphicon glyphicon-download-alt"></span> Download as CSV</button>
        </div>
    </div>
    <div v-else>
        <div v-if="ready" class="row justify-content-center">
                <div class="col-md-6 py-4 text-center">There is no tax event in the selected tax year</div>
        </div>
    </div>
      <!--    <div v-if="!total">There are no any transactions.</div>-->

    <loading :loading="loading"></loading>

  </div>
</template>

<script>
  import axios from 'axios'
  import Decimal from 'decimal.js'
  import moment from 'moment'

  Decimal.set({ toExpNeg: -14 })
  export default {
    created() {
      this.loadTransaction(this.portfolioId)
    },
    props: ['portfolioId'],
    data() {
      return {
        loading: false,
        error: null,
        years: {
          // selected: 1,
          options: [],
        },
        pId: null,
        ready: false,
        transactions: null,
        days: null,
        currencies: null,
        endsums: null,
        endresult: null,
        total: null,
        selectedyear: null,
        enddate: null,
      }
    },
    methods: {
      loadTransaction(portfolioId) {
        this.pId = portfolioId
        let yearsId = 0
        axios.get(`${process.env.API_V1_URL}/portfolio/${portfolioId}/tx/years`)
          .then(res => {
            this.years.options = [{value: '', text: ''}].concat(res.data.map((date, i) => {
              return {value: ++yearsId, text: date}
            }))
          })
      },
      customFixed(str, num = 8) {
        const rgx = new RegExp(`-?\\d+\\.?\\d{0,${num}}`)
        str = (`${str}`).match(rgx)
        return str ? parseFloat(str[0]).toFixed(num) : parseFloat(0).toFixed(num)
      },
      onSelect(v) {
        this.ready = false
        this.error = null
        this.loading = true

        if (this.years.options[v]) {
          return axios.get(`${process.env.API_V1_URL}/portfolio/${this.pId}/tx${this.years.options[v].text ? '/year/' + this.years.options[v].text : ''}`)
            .then(res => {
              this.transactions = res.data.transactions
              this.days = res.data.days
              this.currencies = res.data.currencies
              this.endsums = res.data.endsums
              this.endresult = res.data.endresult
              this.total = res.count
              this.selectedyear = this.years.options[v].text
              this.enddate = res.data.enddate
            })
            .catch(errMsg => {
              this.error = errMsg
            })
            .finally(() => {
              this.loading = false
              this.ready = true
            })
        } else {
          this.ready = false
          this.transactions = false
          this.error = null
          this.loading = false
          return true
        }
      },
      pdf() {
        let pdfMake = require('pdfmake/build/pdfmake.js')
        if (pdfMake.vfs === undefined) {
          let pdfFonts = require('pdfmake/build/vfs_fonts.js')
          pdfMake.vfs = pdfFonts.pdfMake.vfs
        }
        let logoimg = 'data:image/jpeg;base64,'
        let daysdata = [[
          { text: 'Дата', style: 'tableHeader' },
          { text: 'Продажна цена', style: 'tableHeader' },
          { text: 'Цена на придобиване', style: 'tableHeader' },
          { text: 'Обща печалба', style: 'tableHeader' },
          { text: 'Обща загуба', style: 'tableHeader' },
        ]]
        for (let day in this.days) {
          let profit = 0
          let loss = 0
          if (this.days[day].profitLoss > 0) {
            profit = this.days[day].profitLoss.toFixed(2).toString()
          } else if (this.days[day].profitLoss < 0) {
            loss = this.days[day].profitLoss.toFixed(2).toString()
          } else {
            profit = 0
            loss = 0
          }
          daysdata.push([
            day,
            {text: this.days[day].proceeds.toFixed(2).toString(), alignment: 'right'},
            {text: this.days[day].price.toFixed(2).toString(), alignment: 'right'},
            {text: profit, alignment: 'right'},
            {text: loss, alignment: 'right'},
          ])
        }
        let dealsdata = [[
          { text: 'Дата', style: 'tableHeader' },
          { text: 'Номер', style: 'tableHeader' },
          { text: 'Вид транзакция', style: 'tableHeader' },
          { text: 'Продадена валута', style: 'tableHeader' },
          { text: 'Количество продадена валута', style: 'tableHeader' },
          { text: 'Продажна цена (лв.)', style: 'tableHeader' },
          { text: 'Цена на придобиване (лв.)', style: 'tableHeader' },
          { text: 'Резултат от сделката (лв.)', style: 'tableHeader' },
        ]]
        for (let tx in this.transactions) {
          dealsdata.push([
            this.transactions[tx].date,
            this.transactions[tx].transactionId,
            this.$t(this.transactions[tx].type, 'bg'),
            this.transactions[tx].soldCcy,
            {text: this.transactions[tx].soldCcyAmount.toFixed(8).toString(), alignment: 'right'},
            {text: this.transactions[tx].salesProceedsBGN.toFixed(2).toString(), alignment: 'right'},
            {text: this.transactions[tx].costBasisBGN.toFixed(2).toString(), alignment: 'right'},
            {text: this.transactions[tx].profitLoss.toFixed(2).toString(), alignment: 'right'},
          ])
        }
        dealsdata.push([ {colSpan: 8, text: 'Нетен резултат: ' + this.endresult.toFixed(2).toString(), alignment: 'right'} ])
        // console.log(this.$store.getters.getClient)
        let docDefinition = {
          info: {
            title: 'Tax info',
            author: 'blockchain.bg',
          },
          pageMargins: [ 60, 40, 40, 80 ],
          pageSize: 'A4',
          footer: function(currentPage, pageCount) {
            return {
              table: {
                body: [
                  [
                    { text: currentPage.toString() + ' / ' + pageCount, alignment: 'right', style: 'smallText', margin: [60, 20, 50, 40] },
                  ],
                ],
              },
              widths: ['auto'],
              layout: 'noBorders',
            }
          },
          content: [
            {
              style: 'tablePagehead',
              table: {
                headerRows: 1,
                widths: ['*', 'auto', '*'],
                body: [
                  [
                    '',
                    '',
                    { image: logoimg, width: 100, alignment: 'right' },
                  ],
                  [
                    { text: this.$store.getters.getClient.firstName + ' ' + this.$store.getters.getClient.lastName + '\n' + this.$store.getters.getClient.email + '\n#клиентска сметка: ' + this.pId },
                    '',
                    { text: 'Блокчейн-БГ ООД\nЕИК: 204853211\nул. Шипка 6, ет. 3\nСофия 1504, България', alignment: 'right' },
                  ],
                ],
              },
              layout: {
                hLineWidth: function(i, node) {
                  return 0
                },
                vLineWidth: function(i, node) {
                  return 0
                },
              },
            },
            { text: 'Извлечение за ' + this.selectedyear + ' Данъчна година', style: 'header', alignment: 'center' },
            { text: 'Свалено на ' + moment().format('DD.MM.YYYY HH:mm') + ' от app.blockchain.bg', alignment: 'center' },
            { text: 'Таблица 1: Данъчен резултат по дата\n', style: 'subheader', margin: [0, 20, 0, 10] },
            {
              style: 'tableExample',
              table: {
                headerRows: 1,
                widths: ['auto', 'auto', 'auto', 'auto', 'auto'],
                body: daysdata,
              },
              layout: {
                fillColor: function(rowIndex, node, columnIndex) {
                  return ((rowIndex - 1) % 2 === 0 && rowIndex !== 0) ? '#e9ecef' : null
                },
                hLineWidth: function(i, node) {
                  return (i === 0 || i === node.table.body.length) ? 1 : 1
                },
                vLineWidth: function(i, node) {
                  return (i === 0 || i === node.table.widths.length) ? 0 : 0
                },
                hLineColor: function(i, node) {
                  return (i === 0 || i === node.table.body.length) ? 'none' : 'gray'
                },
                vLineColor: function(i, node) {
                  return (i === 0 || i === node.table.widths.length) ? 'none' : 'gray'
                },
                paddingLeft: function(i, node) { return 4 },
                paddingRight: function(i, node) { return 4 },
                paddingTop: function(i, node) { return 3 },
                paddingBottom: function(i, node) { return 3 },
              },
            },
            { text: 'Таблица 2: Данъчен резултат по сделка\n', style: 'subheader', margin: [0, 20, 0, 10] },
            {
              style: 'tableExample',
              table: {
                headerRows: 1,
                widths: ['auto', 'auto', 'auto', 'auto', 'auto', 'auto', 'auto', 'auto'],
                body: dealsdata,
              },
              layout: {
                fillColor: function(rowIndex, node, columnIndex) {
                  return ((rowIndex - 1) % 2 === 0 && rowIndex !== 0) ? '#e9ecef' : null
                },
                hLineWidth: function(i, node) {
                  return (i === 0 || i === node.table.body.length) ? 1 : 1
                },
                vLineWidth: function(i, node) {
                  return (i === 0 || i === node.table.widths.length) ? 0 : 0
                },
                hLineColor: function(i, node) {
                  return (i === 0 || i === node.table.body.length) ? 'none' : 'gray'
                },
                vLineColor: function(i, node) {
                  return (i === 0 || i === node.table.widths.length) ? 'none' : 'gray'
                },
                paddingLeft: function(i, node) { return 4 },
                paddingRight: function(i, node) { return 4 },
                paddingTop: function(i, node) { return 3 },
                paddingBottom: function(i, node) { return 3 },
              },
            },
            {text: '\nВажни бележки и инструкции за попълване на годишна данъчна декларация:\n\n', style: 'bold'},
            {
              ul: [
                'Изготвихме горната информация с цел максимално да Ви улесним при попълване на годишната данъчна декларация (ГДД).',
                'Бихме искали да подчертаем, че данните ще са Ви полезни и правилно изчислени само ако това са единствените Ви сделки с криптовалути - тоест ако сте търгували за собствена сметка извън услугата, която ние предлагаме ще трябва да включите и останалите сделки.',
                'На база на запитване направено от нас към НАП и отговора, който получихме става ясно, че доходите от продажба или замяна на финансови активи, в т.ч. и криптовалута, са в обхвата на доходите, които подлежат на облагане с данък върху общата годишна данъчна основа, като печалбите/загубите се декларират в годишната данъчна декларация по чл. 50 от ЗДДФЛ за годината, през която се смятат за продобити доходите. Казано с по-прости думи - кросовите сделки, които извършваме за Ваша сметка при всяко едно ребалансиране на портфейла са данъчно събитие и реализирания резултат трябва да се декларира в НАП. Ако е положителен се дължи данък, а ако е отрицателен остава само задължението да се декларира.',
                'За целта е необходимо да попълните Приложение № 5 - Доходи от прехвърляне на права или имущество и по конкретно Таблица 2 с код 508 (в поле 2) от ГДД.',
                'Данните в Таблица 1 (Данъчен резултат по дата) от извлечението, което може да свалите от нашата системата са готови за попълване в полета 3-7 в Таблица 2, Приложение № 5 от ГДД.',
                'Понеже ребалансиранията се извършват от нас на чуждестранни борси полета 8-9  “Данни за платеца” се оставят празни и се слага тик на поле 2.2. Ако обаче през данъчната година сте продали криптовалути за фиатни пари чрез българско дружество се слага тик на поле 2.1 и съответно се попълват полета 8 и 9.',
                'При изчислението на данъчния резултат сме използвали цени от следните източници:',
                {
                  ul: [
                    'Курс “Долар/Лев” – референтен курс на БНБ за конкретната дата. При липса на наличен курс се използва предхождащия курс обявен от БНБ.',
                    'Курс “Биткойн/Долар” – цена на затваряне от сайта coinmarketcap.com за конкретната дата',
                  ],
                },
                'В заключение, бихме искали да подчертаем, че ние не сме специалисти по данъчно право и Ви препоръчваме да ползвате услугите на професионален счетоводител при изготвяне и подаване на годишната данъчна декларация. Оставаме на разположение при въпроси от Ваша страна.',
              ],
              style: 'mediumText',
            },
          ],
          styles: {
            header: {
              fontSize: 18,
              bold: true,
              margin: [0, 0, 0, 5],
            },
            subheader: {
              fontSize: 14,
              bold: true,
              margin: [0, 5, 0, 3],
            },
            tableExample: {
              fontSize: 9,
              margin: [0, 5, 0, 10],
            },
            tablePagehead: {
              margin: [0, 0, 0, 40],
            },
            tableHeader: {
              bold: true,
              fontSize: 9,
              color: 'black',
              alignment: 'center',
            },
            bold: {
              bold: true,
            },
            smallText: {
              fontSize: 9,
            },
            mediumText: {
              fontSize: 10,
            },
          },
          defaultStyle: {
            // alignment: 'justify'
          },
        }
        pdfMake.createPdf(docDefinition).download('tax_info_' + this.selectedyear + '_' + moment().format('DD-MM-YYYY') + '.pdf')
      },
      csvExport() {
        let csvContent = ''
        csvContent += '"Блокчейн-БГ ООД ЕИК:204853211, ул.Шипка 6, ет.3, София 1504 България"\n\n"Извлечение за ' + this.selectedyear + ' Данъчна година"\n"Свалено на ' + moment().format('DD.MM.YYYY HH:mm') + ' от app.blockchain.bg"\n'
        csvContent += '"' + this.$store.getters.getClient.firstName + ' ' + this.$store.getters.getClient.lastName + ', ' + this.$store.getters.getClient.email + ', клиентска сметка: ' + this.pId + '"\n\n'
        csvContent += 'Таблица 1: Данъчен резултат по дата\n'
        csvContent += 'Дата;Продажна цена;Цена на придобиване;Обща печалба;Обща загуба\n\n'
        for (let day in this.days) {
          let profit = 0
          let loss = 0
          if (this.days[day].profitLoss > 0) {
            profit = this.days[day].profitLoss.toFixed(2).toString()
          } else if (this.days[day].profitLoss < 0) {
            loss = this.days[day].profitLoss.toFixed(2).toString()
          } else {
            profit = 0
            loss = 0
          }
          csvContent += day + ';' + this.days[day].proceeds.toFixed(2).toString() + ';' + this.days[day].price.toFixed(2).toString() + ';' + profit + ';' + loss + '\n'
        }
        csvContent += '\n\nТаблица 2: Данъчен резултат по сделка\n'
        csvContent += 'Дата;Номер;Вид транзакция;Продадена валута;Количество продадена валута;Продажна цена (лв.);Цена на придобиване (лв.);Резултат от сделката (лв.)\n'
        for (let tx in this.transactions) {
          csvContent += this.transactions[tx].date + ';' + this.transactions[tx].transactionId + ';' + this.$t(this.transactions[tx].type, 'bg') + ';' + this.transactions[tx].soldCcy + ';' + this.transactions[tx].soldCcyAmount.toFixed(8).toString() + ';' + this.transactions[tx].salesProceedsBGN.toFixed(2).toString() + ';' + this.transactions[tx].costBasisBGN.toFixed(2).toString() + ';' + this.transactions[tx].profitLoss.toFixed(2).toString() + '\n'
        }
        csvContent += '\nНетен резултат: ' + this.endresult.toFixed(2).toString()
        csvContent += '\n\nВажни бележки и инструкции за попълване на годишна данъчна декларация:\n'
        csvContent += '"- Изготвихме горната информация с цел максимално да Ви улесним при попълване на годишната данъчна декларация (ГДД)."\n"- Бихме искали да подчертаем, че данните ще са Ви полезни и правилно изчислени само ако това са единствените Ви сделки с криптовалути - тоест ако сте търгували за собствена сметка извън услугата, която ние предлагаме ще трябва да включите и останалите сделки."\n"- На база на запитване направено от нас към НАП и отговора, който получихме става ясно, че доходите от продажба или замяна на финансови активи, в т.ч. и криптовалута, са в обхвата на доходите, които подлежат на облагане с данък върху общата годишна данъчна основа, като печалбите/загубите се декларират в годишната данъчна декларация по чл. 50 от ЗДДФЛ за годината, през която се смятат за продобити доходите. Казано с по-прости думи - кросовите сделки, които извършваме за Ваша сметка при всяко едно ребалансиране на портфейла са данъчно събитие и реализирания резултат трябва да се декларира в НАП. Ако е положителен се дължи данък, а ако е отрицателен остава само задължението да се декларира."\n"- За целта е необходимо да попълните Приложение № 5 - Доходи от прехвърляне на права или имущество и по конкретно Таблица 2 с код 508 (в поле 2) от ГДД."\n"- Данните в Таблица 1 (Данъчен резултат по дата) от извлечението, което може да свалите от нашата системата са готови за попълване в полета 3-7 в Таблица 2, Приложение № 5 от ГДД."\n"- Понеже ребалансиранията се извършват от нас на чуждестранни борси полета 8-9  “Данни за платеца” се оставят празни и се слага тик на поле 2.2. Ако обаче през данъчната година сте продали криптовалути за фиатни пари чрез българско дружество се слага тик на поле 2.1 и съответно се попълват полета 8 и 9."\n"- При изчислението на данъчния резултат сме използвали цени от следните източници:"\n" - Курс “Долар/Лев” – референтен курс на БНБ за конкретната дата. При липса на наличен курс се използва предхождащия курс обявен от БНБ."\n" - Курс “Биткойн/Долар” – цена на затваряне от сайта coinmarketcap.com за конкретната дата"\n"- В заключение, бихме искали да подчертаем, че ние не сме специалисти по данъчно право и Ви препоръчваме да ползвате услугите на професионален счетоводител при изготвяне и подаване на годишната данъчна декларация. Оставаме на разположение при въпроси от Ваша страна."'
        csvContent.replace(/(^\[)|(\]$)/gm, '')
        const data = encodeURIComponent('\uFEFF' + csvContent)
        const link = document.createElement('a')
        // link.setAttribute('href', 'data:text/csv;charset=utf-8,%EF%BB%BF' + data)
        link.setAttribute('href', 'data:text/csv;charset=utf-8,' + data)
        link.setAttribute('download', 'tax_info_' + this.selectedyear + '_' + moment().format('DD-MM-YYYY') + '.csv')
        link.click()
      },
    },
    watch: {
      portfolioId(newId) {
        if (newId) {
          this.loadTransaction(newId)
        }
      },
      '$i18n.locale': function() {
        this.i18n = {
          silentTranslationWarn: false,
        }
      },
    },
  }
</script>
<style scoped>
    .table thead th {
        vertical-align: text-top;
        /*text-align: right;*/
    }
</style>



